import "./SampleModal.css";
import { Button, responsiveFontSizes } from "@material-ui/core";
import { useState, useEffect } from "react";

const KidModal = ({isOpen, setIsOpen, data, setData, tableData, setTableData,setNeedsRefresh, needsRefresh,currentSampleId, setCurrentSampleId}) => {
    const [id, setId] = useState();
    const [options, setOptions] = useState("def");
    const [name1, setName1] = useState("");
    const [name2, setName2] = useState("");
    const [name3, setName3] = useState("");
    const [name4, setName4] = useState("");
    const [status, setStaus] = useState(false);
    const [kidOptions, setKidOptions] = useState([]);

    useEffect(()=>{
        async function fetchKidOptions(){
            const res = await fetch("http://127.0.0.1:8000/api/KidOption/").then(res=>res.json());
            setKidOptions(res);
        }

        fetchKidOptions();
    },[])

    useEffect(()=>{
        
        if(data?.id) setId(data.id);
        if(data?.options) setOptions(data.options);
        if(data?.name1) setName1(data.name1)
        if(data?.name2) setName2(data.name2)
        if(data?.name3) setName3(data.name3)
        if(data?.name4) setName4(data.name4)
        if(data?.status) setStaus(data.status);
        if(data?.id) console.log("This had data")

    },[data])

    const handleSubmit = async() =>{
        if(data){
            const putData = {
                id,
                status,
                options,
                name1,
                name2,
                name3,
                name4,
                sample: currentSampleId
            }
            console.log(putData);
            const requestOptions = {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(putData)
            };
            const response = await fetch(`http://127.0.0.1:8000/api/Kid/${id}`, requestOptions)
                .then(response => response.json());
            console.log(response)
            const result = tableData.map(each=>{
                if(each.id === currentSampleId){
                    each.kids = each.kids.filter(x=>x.id !== response.id)
                    each.kids.push(response)
                }
                return each
            })
            setTableData(result)
            setData(undefined);
            setIsOpen(false);
            clearForm();
            // setNeedsRefresh(needsRefresh+1);            
            return null
        }
        const postData = {
            status,
            options,
            name1,
            name2,
            name3,
            name4,
            sample:currentSampleId
        }
        console.log("Submitting",postData);
        const response = await fetch("http://127.0.0.1:8000/api/Kid/",{
            method:"POST",
            body: JSON.stringify(postData),
            headers:{
                "Content-type": "application/json; charset=UTF-8"
            }
        }).then(res=>res.json());
        console.log(response);
        console.log(tableData);
        const result  = tableData.map(each=>{
            if(each.id === currentSampleId)
            {
                each.kids.push(response);
            }
            return each;
            
        })
        console.log(result);
        setTableData(result);
        setData(undefined);
        clearForm();
        setCurrentSampleId(0);
        setIsOpen(false);
        // console.log("needs refresh",needsRefresh);
        // setNeedsRefresh(needsRefresh + 1);
    }

    const clearForm =()=>{
        setId();
        setOptions("def");
        setName1("");
        setName2("");
        setName3("");
        setName4("");
        setStaus(false);
    }
    return (
        <div id="myModal" style={{
                display: isOpen?"block":"none",
                position: "fixed",
                zIndex: 10,
                left: "0",
                top: "0",
                width: "100%", 
                height: "100%", 
                overflow: "auto", 
                backgroundColor: "rgb(0, 0, 0)", 
                backgroundColor: "rgba(0, 0, 0, 0.4)"
        }}>
            <div className="modal-content">
                {data ?<h2>Edit Kid</h2>:<h2>Add A New Kid</h2> }
               { data? <span style={{marginRight:"10px"}}>
                    <label>ID  </label>
                    <input type="number" value={id} onChange={(e)=>{setId(e.target.value)}}/>
                </span>:""}
                <span style={{marginRight:"10px"}}>
                    <label>Status  </label>
                    <input type="checkbox" checked={status} value={"active"} onChange={()=>{setStaus(!status)}}/>
                </span>
                <span style={{marginRight:"10px"}}>
                    <label>Options  </label>
                    <select name="Options" id="options" style={{width:"10rem"}}
                        value={options}
                        onChange = {(e)=>{
                            setOptions(e.target.value)
                        }}
                    >
                        <option value="def" disabled hidden> Select a Option</option>
                        {kidOptions?.map(each=>{
                            return (
                                <option key={each.id} value={each.option_name}>{each.option_name}</option>
                            )
                        })}
                    </select>
                </span>
                {/* <span style={{marginRight:"10px"}}>
                    <label>Options  </label>
                    <select name="Options" id="options" style={{width:"10rem"}}
                        value={options}
                        onChange = {(e)=>{
                            setOptions(e.target.value)
                        }}
                    >
                        <option value="def" disabled hidden> Select a Option</option>
                        <option value="Option1">Option1</option>
                        <option value="Option2">Option2</option>
                        <option value="Option3">Option3</option>
                        <option value="Option4">Option4</option>
                    </select>
                </span> */}
                <span style={{marginRight:"10px"}}>
                    <label>Name 1  </label>
                    <input type="text" value={name1} onChange={(e)=>{setName1(e.target.value)}}/>
                </span>
                <span style={{marginRight:"10px"}}>
                    <label>Name 2  </label>
                    <input type="text" value={name2} onChange={(e)=>{setName2(e.target.value)}}/>
                </span><br /><br />
                <span style={{marginRight:"10px"}}>
                    <label>Name 3  </label>
                    <input type="text" value={name3} onChange={(e)=>{setName3(e.target.value)}}/>
                </span>
                <span style={{marginRight:"10px"}}>
                    <label>Name 4  </label>
                    <input type="text" value={name4} onChange={(e)=>{setName4(e.target.value)}}/>
                </span>
                <br />
                <div style={{marginTop:"1rem"}}>
                    <Button variant="contained" onClick={handleSubmit}>
                    {data ?"Edit":"Add" }
                    </Button>
                    &nbsp;&nbsp;&nbsp;
                    <Button variant="contained" onClick={()=>{clearForm();setCurrentSampleId();setData();setIsOpen(false)}}>
                        Close
                    </Button>
                </div>
            </div>

        </div>
    )
}

export default KidModal;