import "./SampleModal.css";
import { Button } from "@material-ui/core";
import { useEffect, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Multiselect from 'multiselect-react-dropdown';
Date.prototype.addDays = function(days) {
    var date = new Date(this.valueOf());
    date.setDate(date.getDate() + days);
    return date;
}

var date = new Date();

const SampleModal = ({isOpen, setIsOpen, data, setData, tableData, setTableData,setNeedsRefresh, needsRefresh}) => {
    const [id, setId] = useState();
    const [jobStatus, setJobStatus] = useState("def");
    const [jobCycle, setJobCycle] = useState("def");
    const [feeType, setFeeType] = useState("def");
    const [dateReceived, setDateReceived] = useState(date);
    const [dueDate, setDueDate] = useState(date.addDays(120));
    const [dateOut, setDateOut] = useState(date.addDays(5));
    const [billingStatus, setBillingStatus] = useState(false);
    const [sampleOptions, setSampleOptions] = useState([]);
    const [jobStatusList, setJobStatusList] = useState([]);
    const [jobCycleList, setJobCycleList] = useState([]);
    const [feeTypeList, setFeeTypeList] = useState([]);

    useEffect(()=>{
        async function fetch_job_status_list(){
            const res = await fetch("http://127.0.0.1:8000/api/jobStatusList/").then(res=>res.json());
            setJobStatusList(res);
        }

        fetch_job_status_list();
        async function fetch_job_cycle_list(){
            const res = await fetch("http://127.0.0.1:8000/api/jobCycleList/").then(res=>res.json());
            setJobCycleList(res);
        }

        fetch_job_cycle_list();
        async function fetch_fee_type(){
            const res = await fetch("http://127.0.0.1:8000/api/feeTypeList/").then(res=>res.json());
            setFeeTypeList(res);
        }

        fetch_fee_type();
    },[])

    useEffect(()=>{
        if(data?.id) setId(data.id);
        if(data?.jobStatus) setJobStatusList(data.jobStatus);
        if(data?.jobCycle) setJobCycle(data.jobCycle);
        if(data?.dateReceived) setDateReceived(data.dateReceived)
        if(data?.dueDate) setDueDate(data.dueDate)
        if(data?.dateOut) setDateOut(data.dateOut)
        if(data?.feeType) setFeeType(data.feeType)
        if(data?.billingStatus) setBillingStatus(data.billingStatus);


    },[data])

    // useEffect(()=>{
    //     console.log("data",{id,options,dateReceived,dueDate,dateOut,feeType,billingStatus})
    // },[id,billingStatus,dateReceived,dueDate,dateOut,feeType,options])

    const handleSubmit = async() =>{
        if(data){
            const putData = {
                id,
                billingStatus,
                jobStatusList,
                dateReceived,
                dueDate,
                dateOut,
                feeType
            }
            console.log(putData);
            const requestOptions = {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(putData)
            };
            const res = await fetch(`http://127.0.0.1:8000/api/Sample/${id}`,requestOptions)
                .then(response => response.json());
            console.log("changed sample",res);
            const result = tableData.map(each=>{
                if(each.id === res.id){
                    each.billingStatus = res.billingStatus;
                    each.options = res.options;
                    each.dateReceived = res.dateReceived;
                    each.dueDate = res.dueDate;
                    each.dateOut = res.dateOut;
                    each.feeType = res.feeType;
                }
                return each;
            });
            setTableData(result);
            setData();
            // console.log(tableData);

            setIsOpen(false);
            clearForm();
            // setNeedsRefresh(needsRefresh+1);
            
            return null
        }

       
        const postData = {
            billingStatus,
            dateReceived,
            dueDate,
            dateOut,
            planreview:[]
        }
        console.log("Submitting",postData);
        const response = await fetch("http://127.0.0.1:8000/api/Sample/",{
            method:"POST",
            body: JSON.stringify(postData),
            headers:{
                "Content-type": "application/json; charset=UTF-8"
            }
        }).then(res=>res.json());
        console.log("RESPONSE FROM THE SERVER",response);
        console.log(response);
        console.log(tableData);
        setTableData((prevState)=>[...prevState,response]);
        clearForm();
        setData();
        setNeedsRefresh(needsRefresh+1)
        setIsOpen(false);
    }

    const clearForm =()=>{
        setId();
        setJobStatus("");
        setDateReceived("");
        setJobCycle("");
        setFeeType("");
        setDueDate("");
        setDateOut("");
        setBillingStatus(false);
    }
    
    return (
        <div id="myModal" style={{
            display: isOpen?"block":"none",
            position: "fixed",
            zIndex: 10,
            left: "0",
            top: "0",
            width: "100%", 
            height: "100%", 
            overflow: "auto", 
            backgroundColor: "rgb(0, 0, 0)", 
            backgroundColor: "rgba(0, 0, 0, 0.4)"
    }}>
            <div className="form-box">
            <div className="modal-content">
                {data ?<h2>Edit Sample</h2>:<h2>Add A New Sample</h2> }
               { data? <span style={{marginRight:"10px"}}>
                    <label>ID  </label>
                    <input type="number" value={id} onChange={(e)=>{setId(e.target.value)}}/>
                </span>:""}
                    <label>Job Cycle</label>
                    <div className="selectWrapper">
                        <select name="jobCycleList" id="jobCycleList" className="selectBox"
                            onChange={(e) => {
                                setJobCycle(e.target.value)
                            }}
                        >
                            <option value="def" disabled hidden> Please Select</option>
                            {jobCycleList?.map(each => {
                                return (
                                    <option key={each.id} value={each.name}>{each.name}</option>
                                )
                            })}
                        </select>
                    </div>

                    <label>Job Status</label>
                    <div className="selectWrapper">
                        <select name="jobStatusList" id="jobStatusList" className="selectBox"
                            onChange={(e) => {
                                setJobStatus(e.target.value)
                            }}
                        >
                            <option value="def" disabled hidden> Please Select</option>
                            {jobStatusList?.map(each => {
                                return (
                                    <option key={each.id} value={each.name}>{each.name}</option>
                                )
                            })}
                        </select>
                    </div>



                    <label>Date Received</label>
                    <DatePicker dateFormat="yyyy-MM-dd" selected={dateReceived} onChange={(date) => {setDateReceived(date)}} />
                    <label>Due Date</label>
                    <DatePicker dateFormat="yyyy-MM-dd" selected={dueDate} onChange={(date) => {setDueDate(date)}} />
                    <label>Date Out</label>
                    <DatePicker dateFormat="yyyy-MM-dd" selected={dateOut} onChange={(date) => {setDateOut(date)}} />
                    <label>Fee Type</label>
                    <Multiselect
                        options={feeTypeList} // Options to display in the dropdown
                       
                        onSelect={(e) => {
                            setFeeType(e);
                        }}
                        onRemove={(e) => {
                            setFeeType(e);
                        }}
                        displayValue="name"
                    />
                    {/* <div className="selectWrapper">
                        <select name="feeTypeList" id="feeTypeList" className="selectBox"
                            onChange={(e) => {
                                setFeeType(e.target.value)
                            }}
                        >
                            <option value="def" disabled hidden> Please Select</option>
                            {feeTypeList?.map(each => {
                                return (
                                    <option key={each.id} value={each.id}>{each.name}</option>
                                )
                            })}
                        </select>
                    </div> */}

                    <label>Billing Status</label>
                    <input type="checkbox" value={billingStatus} onChange={(e)=>{setBillingStatus(e.target.value)}}/>
                <br />
                <div style={{marginTop:"1rem"}}>
                    <Button variant="contained" onClick={handleSubmit}>
                    {data ?"Edit":"Add" }
                    </Button>
                    &nbsp;&nbsp;&nbsp;
                    <Button variant="contained" onClick={()=>{clearForm();setData();setIsOpen(false)}}>
                        Close
                    </Button>
                </div>
            </div>
            </div>
        </div>
    )
}

export default SampleModal;