import React, { useEffect } from 'react';
import { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import Container from '@material-ui/core/Container';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';


import { Button } from '@material-ui/core';
const useRowStyles = makeStyles({
  root: {
    '& > *': {
      borderBottom: 'unset',
    },
  },
});
function createData(id,status,options, name1, name2, name3, name4, price) {
  return {
    id,
    status,
    options,
    name1,
    name2,
    name3,
    name4,
    kid: [
      { id: 4, status, options, name1, name2, name3, name4 },
      { id: 5, status, options, name1, name2, name3, name4  },
    ],
  };
}
function Row({tableData,setTableData,row,sample, setSample, isSampleModalOpen, setIsSampleModalOpen, needsRefresh, setNeedsRefresh,setCurrentSampleId,iskidModalOpen,setIsKidModalOpen,kid,setKid}) {
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  const handleSampleDelete = async (id) => {
    await fetch(`http://127.0.0.1:8000/api/Sample/${id}`, { method: 'DELETE' })
    .then(res=>{});
    console.log("sample delete",tableData);
    const result = tableData.filter(each=> each.id !== id);
    setTableData(result);
  }

  const handleSampleEdit = async (id) => {
    console.log("Editing",id);
    const res = await fetch(`http://127.0.0.1:8000/api/Sample/${id}`)
      .then(response => response.json());
    setSample(res[0]);
    setIsSampleModalOpen(true);
  }

  const handleKidDelete = async (id) =>{
    console.log("Deleting kid of id", id);
    await fetch(`http://127.0.0.1:8000/api/Kid/${id}`, { method: 'DELETE' })
    .then(res=>{});
    setNeedsRefresh(needsRefresh+1);
  }

  const handleKidEdit = async (id, sampleId) =>{
    console.log("editing kid ",id)
    const res = await fetch(`http://127.0.0.1:8000/api/Kid/${id}`)
    .then(response => response.json());
    console.log("handlekid edt", res);
    setKid(res[0]);
    setCurrentSampleId(sampleId);
    setIsKidModalOpen(true);
  }
  const handleKidCreate = async (sampleId) =>{
    console.log("creating kid ",sampleId)
    setCurrentSampleId(sampleId);
    setIsKidModalOpen(true);
  }
  return (
    <React.Fragment>
      <TableRow className={classes.root}>
        <TableCell>
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {row.id}
        </TableCell>
        <TableCell component="th" scope="row">
          {row.status}
        </TableCell>
        <TableCell component="th" scope="row">
          {row.type}
        </TableCell>
        <TableCell component="th" scope="row">{row.dateReceived}</TableCell>
        <TableCell component="th" scope="row">{row.dueDate}</TableCell>
        <TableCell component="th" scope="row">{row.dateOut}</TableCell>
        <TableCell component="th" scope="row">{row.feeType}</TableCell>
        <TableCell component="th" scope="row">
                        {row.billingstatus}
                      </TableCell>
                      <TableCell component="th" scope="row">
                        {row.completionStatus}
                      </TableCell>
                      <TableCell component="th" scope="row">
                        {row.evaluation}
                      </TableCell>
                      <TableCell component="th" scope="row">
                        {row.taskCode}
                      </TableCell>
        <TableCell component="th" scope="row">
          <Button onClick={()=>{console.log("Adding a kid on",row.id)}}>
            <AddIcon onClick={()=>{handleKidCreate(row.id)}}/>
          </Button>
          <Button>
            <EditIcon onClick={()=>{handleSampleEdit(row.id)}}/>
          </Button>
          <Button>
            <DeleteIcon onClick={()=>{handleSampleDelete(row.id)}}/>
          </Button>
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Typography variant="h6" gutterBottom component="div">
                Kids
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                  <TableCell>Id</TableCell>
                  <TableCell>Reviewer</TableCell>
                  <TableCell >Dicipline</TableCell>
                  <TableCell >Status</TableCell>
                  <TableCell >Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row?.Plan_reviews?.map((historyRow) => (
                    <TableRow key={historyRow.id}>
                      <TableCell component="th" scope="row">
                        {historyRow.id}
                      </TableCell>
                      <TableCell>{historyRow.reviewer}</TableCell>
                      <TableCell>{historyRow.discipline}</TableCell>
                      <TableCell>{historyRow.status}</TableCell>                      
                      <TableCell>
                      <Button>
                        <EditIcon onClick={()=>{handleKidEdit(historyRow.id, row.id)}}/>
                      </Button>
                      <Button>
                        <DeleteIcon onClick={()=>{handleKidDelete(historyRow.id)}}/>
                      </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

const rows = [
  createData(1,"Active",'option 1', 159, 6.0, 24, 4.0, 3.99),
  createData(2,"Ended",'Option 2', 237, 9.0, 37, 4.3, 4.99),
];
export default function CollapsibleTable({data,setData,sample,setSample,isSampleModalOpen,setIsSampleModalOpen,setNeedsRefresh, needsRefresh,setCurrentSampleId,iskidModalOpen,setIsKidModalOpen,kid,setKid}) {
  return (
    
      <TableContainer component={Paper}>
        <Table aria-label="collapsible table">
          <TableHead>
            <TableRow>
              <TableCell />
              <TableCell>Id</TableCell>
              <TableCell>Status</TableCell>
              <TableCell >Type</TableCell>
              <TableCell >Date Received</TableCell>
              <TableCell >Due Date</TableCell>
              <TableCell >Date Out</TableCell>
              <TableCell>Fee Type</TableCell>
              <TableCell>Billing Status</TableCell>
              <TableCell>Completion Status</TableCell>
              <TableCell>Evaluation</TableCell>
              <TableCell>Task Code</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((each) => (
              <Row 
                key={each.id} 
                row={each} 
                tableData={data} 
                setTableData={setData}
                sample={sample}
                setSample = {setSample}
                isSampleModalOpen ={isSampleModalOpen}
                setIsSampleModalOpen = {setIsSampleModalOpen}
                setNeedsRefresh = {setNeedsRefresh}
                needsRefresh = {needsRefresh}
                setCurrentSampleId={setCurrentSampleId}
                setIsKidModalOpen = {setIsKidModalOpen}
                iskidModalOpen = {iskidModalOpen}
                kid={kid}
                setKid={setKid}
              />
            ))}
          </TableBody>
        </Table>
      </TableContainer>
     
  );
}