import CollapsibleTable from "./component/Tables";
import "./App.css"
import { Button } from "@material-ui/core";
import { useEffect, useState } from "react";
import SampleModal from "./component/SampleModal";
import KidModal from "./component/KidModal";


function App() {
  const [isAddSampleVisible, setIsAddSampleVisible] = useState(false);
  useEffect(()=>{},[isAddSampleVisible])
  const [isOpen, setIsOpen] = useState(false);
  const [sample, SetSample] = useState();
  const [kid, setKid] = useState();
  const [data, setData] = useState([]);
  const [needsRefresh, setNeedsRefresh] = useState(0);
  const [iskidModalOpen, setIsKidModalOpen] = useState(false);
  const [currentSampleId, setCurrentSampleId] = useState(0);

  useEffect(()=>{
    async function fetchSampleData(){
      const res = await fetch('http://127.0.0.1:8000/api/Getusersjobs/')
      .then(response => response.json());
      
    console.log(res);
      setData(res);
    }
    fetchSampleData();
  },[needsRefresh]);

  return (
    <div className="App">
      <h1>Table</h1>
      <SampleModal data={sample} setData={SetSample} setIsOpen={setIsOpen} isOpen={isOpen} tableData={data} setTableData={setData} setNeedsRefresh={setNeedsRefresh} needsRefresh={0}/>
      <KidModal data={kid} setData={setKid} setIsOpen={setIsKidModalOpen} isOpen={iskidModalOpen} tableData={data} setTableData={setData} setNeedsRefresh={setNeedsRefresh} needsRefresh={0} currentSampleId={currentSampleId} setCurrentSampleId={setCurrentSampleId}/>

      <Button variant="contained"  onClick={()=>setIsOpen(true)}>Add a new Record</Button>
      <br/><br />
      <br/><br/>
      <CollapsibleTable data={data} setData={setData} sample={sample} setSample={SetSample} setIsSampleModalOpen={setIsOpen} isSampleModalOpen={isOpen} needsRefresh={needsRefresh} setNeedsRefresh={setNeedsRefresh} setCurrentSampleId={setCurrentSampleId} setIsKidModalOpen={setIsKidModalOpen} iskidModalOpen={iskidModalOpen} kid={kid} setKid={setKid}/>
    </div>
  );
}

export default App;
